import "./NavigationBar.scss";

import React, { useState } from "react";
import { Link } from "react-router-dom";

import Navbar from "react-bootstrap/Navbar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Offcanvas from "react-bootstrap/Offcanvas";

import WoodenButton from "../WoodenButton/WoodenButton";

import HomeLogo from "../../assets/home-logo.png";
import MediaFacebook from "../../assets/media-facebook.svg";
import MediaGithub from "../../assets/media-github.svg";
import MediaMedium from "../../assets/media-medium.svg";
import MediaTelegram from "../../assets/media-telegram.svg";
import MediaTwitter from "../../assets/media-twitter.svg";
import MediaYoutube from "../../assets/media-youtube.svg";
import WhitePaper from "../../assets/white-paper.svg";
import CloseIcon from "../../assets/close-icon.svg";
import MenuIcon from "../../assets/menu-icon.svg";
import ConnectWalletIcon from "../../assets/connect-wallet-icon.svg";

const MEDIA_ICON = [
  { alt: "facebook", src: MediaFacebook, href: "" },
  { alt: "github", src: MediaGithub, href: "" },
  { alt: "medium", src: MediaMedium, href: "" },
  { alt: "telegram", src: MediaTelegram, href: "" },
  { alt: "twitter", src: MediaTwitter, href: "" },
  { alt: "youtube", src: MediaYoutube, href: "" },
  { alt: "white paper", src: WhitePaper, href: "https://eworkcity.gitbook.io/e-work-city-wiki/" },
];

const LINK = [
  { to: "/", text: "HOME" },
  { to: "role", text: "Role" },
  { to: "", text: "Introduce" },
  { to: "", text: "NEWS" },
];

function getMediaIcon(index) {
  let className = "px-1";
  if (index === 0) {
    className = "pe-1";
  } else if (index === 6) {
    className = "ps-3";
  }

  const { alt, src, href } = MEDIA_ICON[index];

  return (
    <Col xs="auto" className={className}>
      <a
        href={href}
        rel="noreferrer"
        target="_blank"
        className="text-white text-decoration-none ms-2"
      >
        <img
          src={src}
          alt={alt}
          className="NavigationBar__Icon"
        />
        {index === 6 && <span className="ms-2">E-Work City Wiki</span>}
      </a>
    </Col>
  );
}

function getLink(index) {
  const { to, text } = LINK[index];
  if (to === "") {
    return <Col xs="auto" className="ps-4 text-white fw-bold">{text}</Col>;
  }
  return (
    <Col xs="auto" className="ps-4 fw-bold">
      <Link to={to} className="text-white text-decoration-none">{text}</Link>
    </Col>
  );
}

function NavigationBar() {
  const [show, setShow] = useState(false);

  return (
    <Navbar
      fixed="bottom"
      expand="xl"
      variant="dark"
      className="NavigationBar fw-bold justify-content-end"
    >
      <Row className="mx-0 align-items-center w-100 flex-nowrap">
        <Col xs="auto" className="p-0 d-xl-none">
          <button
            type="button"
            className="button-invisible fw-bold text-white text-uppercase d-flex align-items-center p-0"
            onClick={() => setShow(!show)}
          >
            <img
              src={show ? CloseIcon : MenuIcon}
              alt={show ? "close" : "menu"}
              style={{ width: "0.8rem", marginRight: "0.1rem" }}
            />
            {show ? "Close" : "Menu"}
          </button>
        </Col>
        <Col xs xl="auto" className="ps-4 pe-2">
          <Row className="justify-content-center">
            <Col xs="auto">
              <Link to="/">
                <img src={HomeLogo} alt="logo" className="NavigationBar__Logo w-100" />
              </Link>
            </Col>
          </Row>
        </Col>
        <Col xs="auto" className="d-none d-xl-flex">
          {getMediaIcon(0)}
          {getMediaIcon(1)}
          {getMediaIcon(2)}
          {getMediaIcon(3)}
          {getMediaIcon(4)}
          {getMediaIcon(5)}
          {getMediaIcon(6)}
        </Col>
        <Col className="d-none d-xl-flex" />
        <Col xs="auto" className="d-none d-xl-flex">
          {getLink(0)}
          {getLink(1)}
          {getLink(2)}
          {getLink(3)}
        </Col>
        <Col xs="auto" className="d-none d-xl-flex">
          <WoodenButton color="black">
            Enter E-WORK CITY
          </WoodenButton>
        </Col>
        <Col
          xs="auto"
          className="fs-sm text-nowrap p-0 d-flex align-items-center text-white"
        >
          <img
            src={ConnectWalletIcon}
            alt=""
            style={{ width: "0.8rem", marginRight: "0.1rem" }}
          />
          Connect Wallet
        </Col>
      </Row>
      <Navbar.Offcanvas show={show} backdrop={false}>
        <Offcanvas.Body className="text-white NavigationBar__Offcanvas">
          <div className="NavigationBar__LogoHolder d-none d-xl-block" />
          <Row
            className="
              align-items-center
              justify-content-lg-between
              justify-content-center
              pt-2
              pt-xl-0
              flex-grow-1"
          >
            <Col xs="auto">
              <Row>
                {getLink(0)}
                <Col xs="12">
                  <hr className="hr-solid" />
                </Col>
                {getLink(1)}
                <Col xs="12">
                  <hr className="hr-solid" />
                </Col>
                {getLink(2)}
                <Col xs="12">
                  <hr className="hr-solid" />
                </Col>
                {getLink(3)}
                <div className="w-100" />
              </Row>
              <Row className="NavigationBar__OffcanvasBlock">
                <Col xs="auto">
                  <WoodenButton color="black">
                    Enter E-WORK CITY
                  </WoodenButton>
                </Col>
              </Row>
              <Row className="NavigationBar__OffcanvasBlock">
                {getMediaIcon(0)}
                {getMediaIcon(1)}
                {getMediaIcon(2)}
                {getMediaIcon(3)}
                {getMediaIcon(4)}
                {getMediaIcon(5)}
                {getMediaIcon(6)}
              </Row>
            </Col>
          </Row>
        </Offcanvas.Body>
      </Navbar.Offcanvas>
    </Navbar>
  );
}

export default NavigationBar;
